import React,{Component} from 'react';
import MuiThemeProvider from 'material-ui/styles/MuiThemeProvider';
import AppBar from 'material-ui/AppBar';
import RaisedButton from 'material-ui/RaisedButton';
import TextField from 'material-ui/TextField';
import axios from 'axios';
import Login from './Login';

const style = {
    margin: 15,
};

class Register extends Component{
    constructor(props){
        super(props);
        this.state = {
            firstname: '',
            lastname: '',
            email: '',
            password: '',
        }
    }
    render(){
        return(
            <div>
                <MuiThemeProvider>
                    <div>
                    <AppBar title="Register" />
                    <TextField hintText="Enter your First Name" floatingLabelText="First Name" onChange={(event,newValue) => this.setState({firstname:newValue})}
                    />
                    <br />
                    <TextField hintText="Enter your Last Name" floatingLabelText="Last Name" onChange={(event,newValue) => this.setState({lastname:newValue})}
                    />
                    <br />
                    <TextField hintText="Enter your Email" floatingLabelText="Emai;" onChange={(event,newValue) => this.setState({email:newValue})}
                    />
                    <br />
                    <TextField type="password" hintText="Enter your Password" floatingLabelText="Password" onChange={(event,newValue) => this.setState({password: newValue})}
                    />
                    <br />
                    <RaisedButton label="Submit" primary={true} style={style} onClick={(event) => this.handleClick(event)} />
                    </div>
                </MuiThemeProvider>
            </div>
        );
    }

    handleClick(event){
        var apiBaseUrl = 'http://104.197.80.225:3010/sq'
        console.log("values",this.state.first_name,this.state.last_name,this.state.email,this.state.password);
        var self = this;
        var payload ={
            "firstname": this.state.firstname,
            "lastname": this.state.lastname,
            "email": this.state.email,
            "password": this.state.password
        }
        axios.post(apiBaseUrl + '/register',payload)
        .then(function(response){
            console.log(response);
            if(response.data.code === 200){
                console.log("Registration Successfull");
                var loginscreen = [];
                loginscreen.push(<Login parentContext={this} />);
                var loginmessage = "Not Registered yet.Go to Registrtion"
                self.props.parentContext.setState({loginscreen:loginscreen,loginmessage:loginmessage,buttonLabel:"Register",isLogin:true});
            }
        })
        .catch(function(error){
            console.log(error)
        });
    }

}



export default Register;


